






















import Vue from 'vue'
export default Vue.extend({
    props: {
        progressCircular: {
            type: Boolean,
            default: false
        },
        text: {
            type: String,
            default: "Loading..."
        }
    },
    data: () => ({
        dialog: false
    }),
    watch: {
        progressCircular() {
            this.dialog = this.progressCircular;
        }
    }
})
