        getMostRecentVote(votes: any[]) {
            const sortedVotes = votes.sort((a, b) => {
                const dateA = new Date(a.createdAt);
                const dateB = new Date(b.createdAt);
                return dateB.getTime() - dateA.getTime();
            });

            return sortedVotes[0];
